const toPreviewImage = (urls) => {
	if (!urls) {
		return;
	}
	if (typeof urls === 'string') {
		urls = [urls];
	}
	urls = urls.map(url => {
		//
		return url.replace(/&thumbnailType=fwsu_100/gi, '');
	});
	uni.previewImage({
		urls: urls,
		longPressActions: {
			itemList: ['', '', ''],
			success: function(data) {
				console.log('' + (data.tapIndex + 1) + ',' + (data.index + 1) + '');
			}
		}
	});
}

const getVideoInfo = (url) => {
	return new Promise((resolve, reject) => {

		uni.getVideoInfo({
			src: url,
			success(res) {
				resolve(res)
			},
			fail(error) {
				reject(error)
			}
		})

	})
}
/**
 * @description 
 */
const getImageInfo = (item) => {
	return new Promise((resolve, reject) => {
		wx.getImageInfo({
			src: item.url,
			success: (res) => {
				resolve({
					name: item.name,
					src: res.path,
					height: res.height,
					h: res.height,
					w: res.width,
					width: res.width
				})
			},
			fail: (err) => {
				reject(err)
			}
		})
	})
}

export {
	getVideoInfo,
	getImageInfo,
	toPreviewImage
}
