import api from '@/common/config.js'

// vmvuethisvuextoken
// getApp().globalDatatokengetApp().globalData
const install = (Vue, vm) => {
	Vue.prototype.$u.http.setConfig({
		baseUrl: api.srvHost || 'https://srvms.100xsys.cn',
		// trueresponseresponse.data
		// truethis.$u.http.interceptor.response
		// originalData: true, 
		// content-type
		// header: {
		// 	'bx_auth_ticket': uni.getStorageSync('bx_auth_ticket')||config.ticket
		// }
	});
	// Token
	Vue.prototype.$u.http.interceptor.request = (config) => {
		if (config.url.indexOf('?openCode=') !== -1) {
			config.header["bx_open_code"] = config.url.split('?openCode=')[1]
			if(config.header.bx_auth_ticket){
				delete config.header.bx_auth_ticket
			}
		}else{
			if(uni.getStorageSync('bx_auth_ticket')||vm.vuex_token){
				config.header.bx_auth_ticket = uni.getStorageSync('bx_auth_ticket')||vm.vuex_token
			}
		}
		// vuextokenuViewvuexhttps://uviewui.com/components/globalVariable.html
		// config.header.token = vm.token;

		// uViewvuex$store.state
		// config.header.token = vm.$store.state.token;

		// tokenglobalDatagetApp().globalData
		// config.header.token = getApp().globalData.username;

		// tokenStorageStorage
		// const token = uni.getStorageSync('token');
		// config.header.token = token;

		return config;
	}
	// 
	Vue.prototype.$u.http.interceptor.response = (res) => {
		// originalDatatrue
		// res.statueCoderes.data.code
		if (res.state == "SUCCESS") {
			// originalDatatruereturnthis.$u.postthen
			return res;
		} else return false;
	}
}

export default {
	install
}
